*{
  margin: 0;
  padding: 0;
}
html{
  font-size: 62.5%;
}
body{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.nav--top{
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 2px 0px #cccccc;
  padding: 10px;
}
.logo{
  width: 100px;
  height: 100px;
  background: transparent;
}
.nav--menu{
  width: 70px;
  outline: 0;
  border: 0;
  height: 70px;
  background: transparent;
  padding: 5px;
}
.nav--menuSub{
  width: 70px;
  height: 70px;
}
.btn--close{
  display: none;
}
.btns{
  cursor: pointer;
}
.buttons{
  transform: translateX(80px);
}
.btn--night{
  transform: translateX(-50px);
}


/* setting up a links in the nav bar */
a{
  text-decoration: none;
  color: rgb(39, 36, 36);
}
a:hover{
  color: rgb(182, 77, 3);
}
.links--cont .links{
  list-style: none;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 600;
  padding: 15px;
  box-shadow: 0 0 3px 3px #cccccc;
}
.links li{
  padding: 8px;
}
/* hiding nav bar  */
.links--cont{
  display: none;
}
.show--links{
  display: block;
}
.btn--open{
  display: block;
}
.btn--menu{
  display: none;
}
.btn--cont{
  transform: translateX(0);
}

/* setting up a media queriees for a bigger screens */
@media screen and (min-width: 780px){
  .btns{
      display: none;
  }
  .btn--night{
      transform: translateX(100rem);
  }
  .buttons{
      display: flex;
  }
 .links--cont{
                  display: flex;
                  flex-direction: row;
              }
  .nav--top{
      justify-content: left;
      box-shadow: none;
  }
  .links--cont .links{
      display: flex;
      box-shadow: none;
      padding: 35px;
  }
  .links li{
      padding: 10px 25px;
  }
  .nav--bar{
      display: flex;
      box-shadow: 0 0 6px 2px #cccccc;
      background: transparent;
  }
  
}


/* setting up a main area  */
.main--area{
  margin-top: 100px;
  margin-left: 80px;
  display: grid;
  grid-template-rows: 40% 60%;
  padding: 15px;
  gap: 20px;
}

.cont1 .title ,
.cont1 .title--sub{
  font-size: 3rem;
}
.cont1 .title--main{
  font-size: 5rem;
  color: rgb(182, 77, 3);
  opacity: 3;
}
.cont1 .content{
  font-size: 1.5rem;
  margin-top: 20px;
  margin-bottom: 20px;
  opacity: .6;
  
}
.cont1 .btn--read{
  font-size: 2rem;
  padding: 8px 40px;
  border-radius: 20px;
  color: #ffffff;
  background-color: rgb(161, 69, 3);
  box-shadow: 0 0 2px 6px rgb(161, 69, 3);
  outline: 0;
  border: 0;
}
.cont2{
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
}
.cont2 .image{
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.5;
}
.cont2 .years{
  font-size: 25rem;
  letter-spacing: 0;
  vertical-align: middle;
  position: absolute;
  color: rgb(161, 69, 3);
  place-items: center;
}

.btn--read::after{
  height: 100%;
  left: -35%;
  top: 0;
  transform: skew(50deg);
  transition-duration: 0.6s;
  transform-origin: top left;
  width: 0;
}
.btn--read:hover::after{
 height: 100%;
 width: 135%;
}

/* setting up a footer */
.icon--cont{
  margin: 80px 80px;
  margin-top: 150px;
}
.icon--cont .icons{
  padding: 15px;
}
.icons:hover{
  background-color: rgb(161, 69, 3);
  border-radius: 50%;
  transform: translateY(-20px);
  transform: scale(15px);
}

/* seting up a media query of max scren size of 1140px */
@media screen and (min-width:900px){
  html{
      font-size: 62.5%;
  }
  .nav--bar{
      width: 100%;
  }
  .btn--night{
      transform: translateX(0);
  }
  .links--cont .links{
      padding: 20px 0;
      font-size: 1.8rem;
  }
  .links li{
      padding: 8px;
  }
  .nav--top{
      padding: 5px;
  }
  .logo{
      width: 100px;
      height: 100px;
  }
  .nav--menu{
      width: 50px;
      height: px;
      padding: 3px;
  }
  .nav--menuSub{
      width: 30px;
      height: 30px;
  }
.main--area{
  grid-template-columns: repeat(2,1fr);
  grid-template-rows: none;
  gap: 0;
  margin-top: 60px;
  margin-left: 40px;
  padding: 0;
}
.cont1 .title ,
.cont1 .title--sub{
  font-size: 2.5rem;
}
.cont1 .title--main{
  font-size: 4rem;
}
.icon--cont{
  margin-top: 50px;
}
}


