      width: 25px;
      height: 25px;
  }
.main--area{
  grid-template-columns: none;
  grid-template-rows: 40% 70%;
  gap: 0;
  margin-top: 45px;
  margin-left: 30px;
  padding: 0;
}
.cont1 .title ,
.cont1 .title--sub{
  font-size: 2.0rem;
}
.cont1 .title--main{
