}
.icon--cont{
  margin-top: 70px;
}
.cont2 .image{
  width: 400px;
}
.cont2{
  justify-content: flex-start;
}
.buttons{
  transform: translateX(0);
}
} 

.img--box {
  width: 200px;
  height: 200px;
}

.title{
  vertical-align: bottom;
}

.cards {
  display: inline;
  flex-direction: column;
}

.news--div {
  display: flex;
  justify-content: space-between;
}

.button--box {
  display: flex;
  justify-content: space-between;
}

.set--center {
  text-align: center;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}